.thumb {
  border-radius: 10px;
  background-color: #ff6060;
  color: #fff;
  font-size: 14px;
  position: relative;
  min-height: 100px;
  max-width: 340px;
  overflow: hidden;
  margin-top: 10px;
}
.thumbBg {
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0) 0%,
    rgba(0, 0, 0, 0.5) 100%
  );
  position: absolute;
  left: 0%;
  right: 0%;
  top: 0%;
  bottom: 0%;
}
.thumbText {
  position: absolute;
  bottom: 10px;
  padding: 20px;
  font-size: 18px;
}
